<template>
    <div id="mapView"></div>
</template>

<script>
import  { mapActions, mapGetters }  from    'vuex'
export default {
    data() {
        return {
            indexLevelConst       : ''  // : ; ( :  / :  )
            ,openMapViewNum_const : ''  // 
            ,indexLevelNum_const  : ''  // 
            ,tempIndexLevel       : ''  // 
        }
    }
    ,mounted: function() {
        //  
        this.firstSaveIndexLevel()
    }
    ,methods: {
        // : 
        firstSaveIndexLevel: function() {
            // : ( ,  )
            this.$store.dispatch({
                type: 'firstLoadingMap',
                cityCode: this.$store.state.city.cityCode                                     //   
            })
            // , 
            this.$data.openMapViewNum_const = this.$store.state.searchValue.openMapViewNum_Arr.length    // 
            // 
            this.$data.indexLevelConst      = this.$store.state.searchValue.indexLevel
        }
        // : (  )
        ,loadingBaiduMap: function() {
            let city_Longitude = this.$store.state.city.cityLongitude                        // 
                ,city_Latitude  = this.$store.state.city.cityLatitude                        // 
            const miniMap = new BMap.Map("mapView", {enableMapClick:false})                  // Map()
            const miniMapPoint = new BMap.Point( city_Longitude, city_Latitude)              // (  )
            miniMap.centerAndZoom(miniMapPoint,11)                                           // ,15
            miniMap.addControl(new BMap.NavigationControl({
                anchor: BMAP_ANCHOR_TOP_RIGHT
                ,type: BMAP_NAVIGATION_CONTROL_SMALL
            }))
            /*    */
            // 
            let administrative_Arr    = []
                administrative_Arr    = this.$store.state.searchValue.administrative_Arr    // 
            let business_Arr          = []
                business_Arr          = this.$store.state.searchValue.business_Arr          // 
            let building_Arr          = []
                building_Arr          = this.$store.state.searchValue.building_Arr          // 
            // 
            let lastLevel;
            miniMap.addEventListener("zoomstart",function(){
                lastLevel = this.getZoom()
            })
            miniMap.addEventListener("zoomend", function(){
                let zoomLevel = this.getZoom();                                                 //
                if (zoomLevel >= 15){                                                           // 3:
                    console.log("3:")
                    // addBuilding(buildingPoint_Arr,17);
                    saveIndexLevel('building')                                                  // : 
                    setIndexLevelNum()                                                          // , (  )
                }else if (zoomLevel >= 14){
                    console.log("2:")                                          // 
                    addRangeOverlay(business_Arr,16)
                    saveIndexLevel('business')                                                  // : 
                    setIndexLevelNum()                                                          // , (  )
                }else if (zoomLevel >= 12){
                    console.log("1:(  )")                            // 
                    addRangeOverlay(administrative_Arr,14)
                    saveIndexLevel('administrative')                                            // : 
                    setIndexLevelNum()                                                          // , (  )
                }else{
                    if (!lastLevel < 12) {
                        console.log("1:(  )");
                        addRangeOverlay(administrative_Arr,14);                                 // 
                        setRangeOverlayStyle()                                                  // (  )
                        saveIndexLevel('administrative')                                        // : 
                    }
                }
            })

            // store, 
            const saveIndexLevel = ( indexLevel ) => {
                // $data, $data, 
                this.$data.indexLevelConst = {}                                                                 // 
                this.$data.indexLevelConst = this.$store.state.searchValue.indexLevel                           // 
                this.$data.indexLevelNum_const = this.$store.state.searchValue.setIndexLevelNum_Arr.length      // 
                this.$store.dispatch({                                                                          // : 
                    type: 'setMapIndexLevel'
                    ,indexLevel: indexLevel
                })
            }
            // , 
            const setIndexLevelNum = () => {
                this.$store.dispatch({                                                          // : 
                    type: 'setIndexLevelNum'
                })
            }
            //    (  +  +  )
            const buildingOverlayArr = []                                                         // 
            const addRangeOverlay = ( ObjGroup,setZoom ) => {                                     // 1 + 2 
                miniMap.clearOverlays()                                                           // 
                for(let i=0; i<ObjGroup.length; i++) {
                    let arr  = new Object();
                        arr  = ObjGroup[i];
                    let code    = arr.code
                        ,text   = arr.name + "<br />" + arr.resourceAmount + ""                   // 
                        ,zoom   = setZoom;
                    let RangeOverlay = new rangeOverlay(
                        new BMap.Point(arr.latitude,arr.longitude),text,code,zoom
                    );
                    // console.log(''+ i);
                    miniMap.addOverlay(RangeOverlay);
                }
            };
            //  1,2
            function rangeOverlay(point,text,code,zoom){
                this._point = point;
                this._text  = text;
                this._code  = code;
                this._zoom  = zoom;
            }
            rangeOverlay.prototype = new BMap.Overlay();
            rangeOverlay.prototype.initialize = function(map){
                this._map = map;
                var div = this._div = document.createElement("div");
                div.setAttribute("id",this._code);
                div.setAttribute("class","range-overlay--big");
                div.style.zIndex = BMap.Overlay.getZIndex(this._point.lat);
                // code
                var code = this._code,   //
                    point = this._point,
                    zoom = this._zoom;
                div.onclick = function businessCirclePoint(){
                    // Ajaxcode
                    map.setZoom(zoom);      // ,
                    // console.log("" + url);
                }
                var span = this._span = document.createElement("span");
                div.appendChild(span);
                div.getElementsByTagName("span")[0].innerHTML =  this._text;
                div.onmouseover = function(){ this.style.zIndex = "9"; }
                div.onmouseout = function(){ this.style.zIndex = "1"; }
                map.getPanes().labelPane.appendChild(div);
                return div;
            }
            rangeOverlay.prototype.draw = function(){
                var map = this._map;
                //  ( ,.. )
                var pointA = {}
                pointA.lat = this._point.lng
                pointA.lng = this._point.lat
                var pixel = map.pointToOverlayPixel(pointA)
                this._div.style.left = pixel.x - 40 + "px"
                this._div.style.top  = pixel.y - 40 + "px"
            }
            // 1( 12  )
            const setRangeOverlayStyle = () => {
                for(let i = 0; i < administrative_Arr.length; i++){
                    let rangeOverlay_Obj = document.getElementById(administrative_Arr[i].code)
                    rangeOverlay_Obj.setAttribute('class','range-overlay')
                }
            }
            // , 
            addRangeOverlay(administrative_Arr,14)                                           // (  , 11 )
            setRangeOverlayStyle()                                                           // (  )

        }
        // :  
        ,updateIndexLevel: function() {

        }
        // : , 
        ,judgeState: function() {
            // state
            let indexLevel              = this.$store.state.searchValue.indexLevel                          // 
                ,btypeState             = this.$store.state.searchValue.btype                               // 
                ,requestLength          = this.$store.state.searchMapRequest_Arr.length                     // 
                ,openMapViewNumLength   = this.$store.state.searchValue.openMapViewNum_Arr.length           // 
                ,indexLevelNumLength    = this.$store.state.searchValue.setIndexLevelNum_Arr.length         // 
                ,saveIndexLevelNum    = this.$store.state.searchValue.saveIndexLevelNum_Arr                 // (  )
            // data
            let indexLevelConst         = this.$data.indexLevelConst                                        // data: 
                ,openMapViewNum_const   = this.$data.openMapViewNum_const                                   // data: 
                ,indexLevelNum_const    = this.$data.indexLevelNum_const                                    // data: 


            // 1. 
            // 1.1   1( 1: , ; 1: , ,  )

            if( openMapViewNumLength == 1 ) {
                if( requestLength == 1 ) {
                    console.log(' - ')
                    this.loadingBaiduMap()
                } else {
                    // 
                    if ( indexLevelConst == indexLevel ) {
                        // 
                        console.log(' -  - ')
                    } else {
                        // : (  )
                        this.$store.dispatch({
                            type: 'saveIndexLevelNum',
                            saveNum: indexLevelNum_const
                        })
                        //  -  -  - ( 2 )
                        if( saveIndexLevelNum.length != 1 ) {
                            let saveIndexLevelNum_lengthNum = saveIndexLevelNum.length
                                ,minNum = this.$store.state.searchValue.saveIndexLevelNum_Arr[saveIndexLevelNum_lengthNum-2].saveNum            // 2 -> 0
                                ,maxNum = this.$store.state.searchValue.saveIndexLevelNum_Arr[saveIndexLevelNum_lengthNum-1].saveNum            // 1 -> 1
                            if( minNum == maxNum ) {
                                console.log(' -  -  - , ')
                            } else {
                                console.log(' -  -  - , ')
                            }
                        }
                    }
                }
            } else {
                // (  ) 
                console.log('')
                this.loadingBaiduMap()
            }
        }
    }
    ,computed: mapGetters({
        getSearchRequest: 'getSearchRequest'
    })
    ,watch: {
        //  '' , 
        getSearchRequest: function () {
            this.updateIndexLevel()             // 
            this.judgeState()                   // 
        }
    }
}
</script>

<style lang="sass" scoped>
@import '../../sass/main'

</style>



